// components/dynamic-zone/how-it-works/index.tsx
'use client';

import React, { useEffect, useMemo, useRef, useState } from 'react';
import {
  animate,
  motion,
  useMotionValue,
  useMotionValueEvent,
  useReducedMotion,
  type AnimationPlaybackControls,
} from 'framer-motion';
import { Calendar, Coffee, Diamond, Sparkles, type LucideIcon } from 'lucide-react';

import { Container } from '../../container';
import { Heading } from '../../elements/heading';
import { Subheading } from '../../elements/subheading';

type StepInput = {
  title?: string | null;
  description?: string | null;
  ctaText?: string | null;
  ctaHref?: string | null;
};

type Step = {
  title: string;
  description: string;
  ctaText?: string;
  ctaHref?: string;
  icon: LucideIcon;
};

type Pt = { x: number; y: number };

const clamp = (n: number, min: number, max: number) => Math.max(min, Math.min(max, n));

const quad = (t: number, p0: Pt, p1: Pt, p2: Pt): Pt => {
  const u = 1 - t;
  return {
    x: u * u * p0.x + 2 * u * t * p1.x + t * t * p2.x,
    y: u * u * p0.y + 2 * u * t * p1.y + t * t * p2.y,
  };
};

export const HowItWorks = ({
  heading,
  sub_heading,
  steps,
}: {
  heading?: string | null;
  sub_heading?: string | null;
  steps?: StepInput[] | null;
}) => {
  const reduce = useReducedMotion();

  const fallback: Omit<Step, 'icon'>[] = useMemo(
    () => [
      {
        title: 'Időpont Foglalás',
        description:
          'Kezdje meg útját a megújulás felé. Válassza ki az Önnek legmegfelelőbb időpontot kényelmes online rendszerünkön keresztül, vagy hívjon minket bizalommal egyedi igényeivel.',
        ctaText: 'Foglalás Most',
        ctaHref: '#',
      },
      {
        title: 'Konzultáció',
        description:
          'Személyes találkozó szakértőinkkel, ahol részletesen felmérjük igényeit, bőrét és stílusát. Egy közös beszélgetés során megalkotjuk az Önre szabott, egyedi kezelési tervet.',
      },
      {
        title: 'Szolgáltatás',
        description:
          'Dőljön hátra és élvezze a prémium kezelés nyugodt, luxus környezetében. A legkiválóbb anyagok és legmodernebb technikák felhasználásával varázsoljuk újjá megjelenését.',
      },
      {
        title: 'Utóápolás',
        description:
          'A törődés nem ér véget a szalonban. Szakértői tanácsokkal és professzionális termékekkel látjuk el, hogy a tartós, ragyogó eredményt otthon is megőrizhesse.',
        ctaText: 'Kapcsolatfelvétel',
        ctaHref: '#',
      },
    ],
    []
  );

  const iconByIndex: LucideIcon[] = useMemo(() => [Calendar, Coffee, Diamond, Sparkles], []);

  const resolvedSteps: Step[] = useMemo(() => {
    const src = Array.isArray(steps) && steps.length ? steps : fallback;
    return src.map((s, i) => ({
      title: (s.title ?? fallback[i]?.title ?? `Lépés ${i + 1}`) as string,
      description: (s.description ?? fallback[i]?.description ?? '') as string,
      ctaText: (s as any).ctaText ?? fallback[i]?.ctaText ?? undefined,
      ctaHref: (s as any).ctaHref ?? fallback[i]?.ctaHref ?? undefined,
      icon: iconByIndex[i] ?? Sparkles,
    }));
  }, [steps, fallback, iconByIndex]);

  const len = resolvedSteps.length;
  const [active, setActive] = useState(0);
  const [auto, setAuto] = useState<boolean>(() => !reduce);
  const [detailsOpen, setDetailsOpen] = useState(false);

  const safeActive = clamp(active, 0, Math.max(0, len - 1));
  const activeStep = resolvedSteps[safeActive];

  // ---- panel width ----
  const panelRef = useRef<HTMLDivElement>(null);
  const [vw, setVw] = useState(0);

  useEffect(() => {
    const el = panelRef.current;
    if (!el) return;
    const ro = new ResizeObserver((entries) => {
      const w = entries?.[0]?.contentRect?.width ?? 0;
      setVw(w);
    });
    ro.observe(el);
    return () => ro.disconnect();
  }, []);

  // ---- motion ----
  const x = useMotionValue(0);

  const animCtrlRef = useRef<AnimationPlaybackControls | null>(null);
  const isDraggingRef = useRef(false);
  const dragStartXRef = useRef(0);

  const pauseUntilRef = useRef(0);
  const lastLiveRef = useRef(0);
  const [liveActive, setLiveActive] = useState(0);

  const pauseAuto = (ms = 9000) => {
    pauseUntilRef.current = Date.now() + ms;
  };

  const geom = useMemo(() => {
    const viewportW = Math.max(340, vw || 0);

    const H = viewportW < 560 ? 180 : 210;
    const virtualW = Math.max(viewportW * 1.9, viewportW + 560);

    const pad = Math.max(52, virtualW * 0.08);

    // baseline sits lower; arc should be VERY flat (big radius) like the reference
    const baseY = H - (viewportW < 560 ? 56 : 66);
    const depth = viewportW < 560 ? 16 : 22; // smaller = flatter => less local "slant"
    const peakY = baseY - depth;

    const p0: Pt = { x: pad, y: baseY };
    const p1: Pt = { x: virtualW / 2, y: peakY };
    const p2: Pt = { x: virtualW - pad, y: baseY };

    const nodesT =
      len <= 1
        ? [0.5]
        : resolvedSteps.map((_, i) => {
            const raw = len === 1 ? 0.5 : i / (len - 1);
            return 0.08 + raw * 0.84;
          });

    const nodePts = nodesT.map((t) => quad(t, p0, p1, p2));
    const centerX = viewportW / 2;

    const snaps = nodePts.map((pt) => centerX - pt.x);
    const minSnap = Math.min(...snaps);
    const maxSnap = Math.max(...snaps);

    const slack = Math.max(44, viewportW * 0.06);
    const dragMin = minSnap - slack;
    const dragMax = maxSnap + slack;

    const yMid = quad(0.5, p0, p1, p2).y;
    const markerTop = yMid - (viewportW < 560 ? 62 : 78);
    const markerBottom = yMid + (viewportW < 560 ? 52 : 64);

    return {
      viewportW,
      H,
      virtualW,
      p0,
      p1,
      p2,
      nodePts,
      snaps,
      dragMin,
      dragMax,
      yMid,
      markerTop,
      markerBottom,
    };
  }, [vw, len, resolvedSteps]);

  const nearestIndex = (xVal: number) => {
    let best = 0;
    let bestD = Infinity;
    for (let i = 0; i < geom.snaps.length; i++) {
      const d = Math.abs(xVal - geom.snaps[i]);
      if (d < bestD) {
        bestD = d;
        best = i;
      }
    }
    return best;
  };

  const stopAnim = () => {
    animCtrlRef.current?.stop();
    animCtrlRef.current = null;
  };

  const snapTo = (i: number, soft = false) => {
    const idx = clamp(i, 0, Math.max(0, len - 1));
    const target = geom.snaps[idx] ?? 0;

    stopAnim();

    if (reduce) {
      x.set(target);
      return;
    }

    animCtrlRef.current = animate(
      x,
      target,
      soft
        ? { duration: 0.28, ease: [0.22, 1, 0.36, 1] }
        : { type: 'spring', stiffness: 240, damping: 30 }
    );
  };

  // initial snap on resize
  useEffect(() => {
    snapTo(safeActive, true);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [geom.viewportW]);

  // snap on active change
  useEffect(() => {
    snapTo(safeActive);
    setDetailsOpen(false);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [safeActive]);

  useMotionValueEvent(x, 'change', (latest) => {
    const now = performance.now();
    if (now - lastLiveRef.current < 60) return;
    lastLiveRef.current = now;
    setLiveActive(nearestIndex(latest));
  });

  // auto-loop
  useEffect(() => {
    if (reduce) return;
    if (!auto) return;
    if (len <= 1) return;

    const id = window.setInterval(() => {
      if (isDraggingRef.current) return;
      if (Date.now() < pauseUntilRef.current) return;
      setActive((a) => (a + 1) % len);
    }, 3400);

    return () => window.clearInterval(id);
  }, [auto, reduce, len]);

  const ticks = useMemo(() => {
    const count = geom.viewportW < 560 ? 50 : 72;
    return Array.from({ length: count }, (_, i) => i);
  }, [geom.viewportW]);

  const onPick = (i: number) => {
    pauseAuto(11000);
    setActive(i);
  };

  const onTrackPointerDown: React.PointerEventHandler<HTMLDivElement> = (e) => {
    pauseAuto(11000);
    stopAnim();

    // Don't "snap-freeze" if the user clicked a milestone node button
    const el = e.target as HTMLElement | null;
    if (el?.closest('[data-milestone-node="true"]')) return;

    const snap = geom.snaps[safeActive];
    if (typeof snap === 'number') x.set(snap);
  };

  const onDragStart = () => {
    pauseAuto(11000);
    isDraggingRef.current = true;
    stopAnim();

    const snap = geom.snaps[safeActive];
    if (typeof snap === 'number') x.set(snap);

    dragStartXRef.current = x.get();
  };

  const onDragEnd = () => {
    isDraggingRef.current = false;

    const endX = x.get();
    const delta = endX - dragStartXRef.current;

    // "Tap" / micro-move threshold: do NOT change station
    if (Math.abs(delta) < 8) {
      snapTo(safeActive, true);
      return;
    }

    const idx = nearestIndex(endX);
    setActive(idx);
  };

  const onKeyDown: React.KeyboardEventHandler<HTMLDivElement> = (e) => {
    if (e.key === 'ArrowLeft') {
      e.preventDefault();
      pauseAuto(11000);
      const next = clamp(safeActive - 1, 0, len - 1);
      setActive(next);
    }
    if (e.key === 'ArrowRight') {
      e.preventDefault();
      pauseAuto(11000);
      const next = clamp(safeActive + 1, 0, len - 1);
      setActive(next);
    }
  };

  return (
    <section className="relative">
      <Container className="py-20 md:py-28 max-w-7xl mx-auto">
        {(heading || sub_heading) && (
          <header className="mx-auto mb-10 max-w-4xl text-center">
            {heading ? <Heading className="text-center">{heading}</Heading> : null}
            {sub_heading ? (
              <Subheading className="mx-auto mt-4 max-w-2xl text-center">{sub_heading}</Subheading>
            ) : null}
          </header>
        )}

        <div
          ref={panelRef}
          tabIndex={0}
          onKeyDown={onKeyDown}
          className={[
            'relative overflow-hidden rounded-[28px] md:rounded-[32px]',
            'border border-white/10',
            'bg-[#0b0c0e]',
            'outline-none',
            'shadow-[0_40px_140px_rgba(0,0,0,0.55)]',
            'min-h-[560px] md:min-h-[640px]',
          ].join(' ')}
        >
          {/* vignette + lights */}
          <div
            aria-hidden
            className="pointer-events-none absolute inset-0"
            style={{
              backgroundImage:
                'radial-gradient(900px circle at 20% 12%, rgba(255,255,255,0.09), transparent 58%), radial-gradient(760px circle at 78% 18%, rgba(255,255,255,0.07), transparent 62%), radial-gradient(1200px circle at 50% 120%, rgba(0,0,0,0.85), transparent 58%), radial-gradient(1200px circle at 50% 50%, rgba(0,0,0,0.55), transparent 70%)',
            }}
          />

          {/* subtle grain */}
          <div aria-hidden className="pointer-events-none absolute inset-0 opacity-[0.10] mix-blend-overlay">
            <svg className="h-full w-full" preserveAspectRatio="none" xmlns="http://www.w3.org/2000/svg">
              <filter id="noiseFilter">
                <feTurbulence type="fractalNoise" baseFrequency="0.9" numOctaves="3" stitchTiles="stitch" />
              </filter>
              <rect width="100%" height="100%" filter="url(#noiseFilter)" opacity="0.6" />
            </svg>
          </div>

          {/* content */}
          <div className="relative z-10 flex h-full flex-col items-center px-6 py-10 md:px-10 md:py-12">
            {/* top center: auto-loop */}
            <div className="mt-2 text-center">
              <div className="text-sm font-medium text-white/85">Automatikus váltás</div>

              <div className="mt-4 inline-flex rounded-full bg-white/10 p-1">
                <button
                  type="button"
                  onClick={() => {
                    pauseAuto(2500);
                    setAuto(true);
                  }}
                  className={[
                    'px-6 py-2 text-sm rounded-full transition',
                    'focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-white/30',
                    auto
                      ? 'bg-white/15 text-white shadow-[inset_0_0_0_1px_rgba(255,255,255,0.08)]'
                      : 'text-white/65 hover:text-white/80',
                  ].join(' ')}
                >
                  Igen
                </button>
                <button
                  type="button"
                  onClick={() => {
                    pauseAuto(2500);
                    setAuto(false);
                  }}
                  className={[
                    'px-6 py-2 text-sm rounded-full transition',
                    'focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-white/30',
                    !auto
                      ? 'bg-white/15 text-white shadow-[inset_0_0_0_1px_rgba(255,255,255,0.08)]'
                      : 'text-white/65 hover:text-white/80',
                  ].join(' ')}
                >
                  Nem
                </button>
              </div>

              <div className="mt-7 max-w-xl text-center text-sm leading-relaxed text-white/55">
                Kattints a pontokra, vagy húzd az ívet – billentyűzeten a nyilakkal is navigálhatsz.
              </div>
            </div>

            {/* arc area */}
            <div className="relative mt-12 w-full flex-1">
              {/* center marker (muted) */}
              <div aria-hidden className="pointer-events-none absolute left-1/2 top-0 bottom-0 w-px">
                <div
                  className="absolute w-px"
                  style={{
                    top: `${Math.max(0, geom.markerTop)}px`,
                    height: `${Math.max(0, geom.markerBottom - geom.markerTop)}px`,
                    transform: 'translateX(-0.5px)',
                    background:
                      'linear-gradient(to bottom, rgba(178,147,61,0), rgba(178,147,61,0.95), rgba(178,147,61,0))',
                  }}
                />
              </div>

              <div className="relative mx-auto mt-6 h-[220px] md:h-[260px] w-full overflow-hidden">
                <motion.div
                  className="absolute left-0 top-0 h-full touch-pan-y"
                  style={{ width: geom.virtualW, x }}
                  drag={reduce ? false : 'x'}
                  dragMomentum={false}
                  dragElastic={0.08}
                  dragConstraints={reduce ? undefined : { left: geom.dragMin, right: geom.dragMax }}
                  onPointerDown={onTrackPointerDown}
                  onDragStart={reduce ? undefined : onDragStart}
                  onDragEnd={reduce ? undefined : onDragEnd}
                >
                  {/* arc baseline (EXTREMELY subtle) */}
                  <svg
                    aria-hidden
                    width={geom.virtualW}
                    height={geom.H}
                    viewBox={`0 0 ${geom.virtualW} ${geom.H}`}
                    className="absolute inset-0"
                  >
                    <path
                      d={`M ${geom.p0.x} ${geom.p0.y} Q ${geom.p1.x} ${geom.p1.y} ${geom.p2.x} ${geom.p2.y}`}
                      fill="none"
                      stroke="rgba(255,255,255,0.12)"
                      strokeWidth="1"
                    />
                  </svg>

                  {/* ticks (VERTICAL, no rotate) */}
                  {ticks.map((i) => {
                    const t = i / (ticks.length - 1);
                    const tt = 0.06 + t * 0.88;
                    const p = quad(tt, geom.p0, geom.p1, geom.p2);

                    const majorEvery = ticks.length < 60 ? 12 : 16;
                    const midEvery = ticks.length < 60 ? 6 : 8;

                    const major = i % majorEvery === 0;
                    const mid = i % midEvery === 0;

                    const isMobile = geom.viewportW < 560;
                    const L = major
                      ? isMobile
                        ? 28
                        : 34
                      : mid
                      ? isMobile
                        ? 18
                        : 22
                      : isMobile
                      ? 10
                      : 12;

                    const op = major ? 0.26 : mid ? 0.18 : 0.12;

                    return (
                      <span
                        key={`tick-${i}`}
                        aria-hidden
                        className="absolute w-px bg-white"
                        style={{
                          left: p.x,
                          top: p.y - L,
                          height: L,
                          opacity: op,
                          transform: 'translateX(-0.5px)',
                        }}
                      />
                    );
                  })}

                  {/* nodes */}
                  {resolvedSteps.map((s, i) => {
                    const pt = geom.nodePts[i];
                    const isNear = i === liveActive;
                    const Icon = s.icon;

                    return (
                      <button
                        key={`node-${i}`}
                        type="button"
                        data-milestone-node="true"
                        onClick={() => onPick(i)}
                        className="absolute"
                        style={{
                          left: pt.x,
                          top: pt.y,
                          transform: 'translate(-50%, -50%)',
                        }}
                        aria-label={`${String(i + 1).padStart(2, '0')} – ${s.title}`}
                      >
                        <motion.div
                          initial={false}
                          animate={
                            reduce
                              ? undefined
                              : isNear
                              ? { scale: 1, opacity: 1 }
                              : { scale: 0.98, opacity: 0.82 }
                          }
                          transition={reduce ? undefined : { duration: 0.22, ease: [0.22, 1, 0.36, 1] }}
                          className={[
                            'grid h-12 w-12 place-items-center rounded-full',
                            'bg-black/25',
                            'ring-1 ring-white/14',
                            'focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-white/25',
                          ].join(' ')}
                          style={{
                            boxShadow: isNear
                              ? '0 14px 40px rgba(0,0,0,0.45), inset 0 0 0 10px rgba(0,0,0,0.25)'
                              : '0 10px 26px rgba(0,0,0,0.35), inset 0 0 0 10px rgba(0,0,0,0.18)',
                          }}
                        >
                          <Icon
                            className="h-5 w-5"
                            aria-hidden
                            style={{
                              color: 'rgba(255,255,255,0.82)',
                              opacity: isNear ? 1 : 0.7,
                            }}
                          />
                        </motion.div>

                        <div className="mt-2 text-[11px] font-medium text-white/40">
                          {String(i + 1).padStart(2, '0')}
                        </div>
                      </button>
                    );
                  })}
                </motion.div>
              </div>

              {/* bottom center active */}
              <div className="mt-4 flex flex-col items-center text-center">
                <activeStep.icon className="h-7 w-7 text-white/85" aria-hidden />
                <div className="mt-3 text-lg font-semibold tracking-tight text-white">{activeStep.title}</div>

                {/* details (collapsible, keeps Strapi description) */}
                <motion.div
                  initial={false}
                  animate={{ height: detailsOpen ? 'auto' : 0, opacity: detailsOpen ? 1 : 0 }}
                  transition={reduce ? { duration: 0.15 } : { duration: 0.28, ease: [0.22, 1, 0.36, 1] }}
                  className="overflow-hidden"
                >
                  <div className="mx-auto mt-4 max-w-2xl text-sm leading-relaxed text-white/58">
                    {activeStep.description}
                    {activeStep.ctaText && activeStep.ctaHref ? (
                      <div className="mt-4">
                        <a
                          href={activeStep.ctaHref}
                          className="inline-flex items-center gap-2 text-sm text-white/80 underline underline-offset-4 hover:text-white"
                        >
                          {activeStep.ctaText}
                          <span aria-hidden className="text-white/60">
                            →
                          </span>
                        </a>
                      </div>
                    ) : null}
                  </div>
                </motion.div>
              </div>
            </div>

            {/* bottom right details link */}
            <div className="absolute bottom-8 right-8 md:bottom-10 md:right-10">
              <button
                type="button"
                onClick={() => {
                  pauseAuto(11000);
                  setDetailsOpen((v) => !v);
                }}
                className="text-sm text-white/60 hover:text-white/80 underline-offset-4 hover:underline focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-white/25 rounded"
                aria-expanded={detailsOpen}
              >
                Részletek
              </button>
            </div>
          </div>
        </div>

        {/* Keep Strapi mapping in structure (for later CMS extensions / debug) */}
        <div className="sr-only" aria-hidden>
          {resolvedSteps.map((s, i) => (
            <span key={`sr-step-${i}`}>{s.title}</span>
          ))}
        </div>
      </Container>
    </section>
  );
};
